<template>
	<div>
		<datail-banner
      :sightName="sightName"
      :bannerImg="bannerImg"
      :gallaryImgs="gallaryImgs"
    ></datail-banner>
		<datail-header></datail-header>
		<div class="content">
			<datail-list :list="list"></datail-list>
		</div>

	</div>
</template>
<script>
import DatailBanner from './components/Banner'
import DatailHeader from './components/Header'
import DatailList from './components/List'
import axios from 'axios'
export default {
	name:'Datail',
	components: {
		DatailBanner,
		DatailHeader,
		DatailList
	},
	data () {
		return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      list: []
		}
	},
  methods: {
    getDatailInfo () {
      axios.get('https://raw.githubusercontent.com/yangrui123456/Travel/master/static/mock/detail.json', {params: {id: this.$route.params.id}}).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
        this.gallaryImgs = data.gallaryImgs
        this.list = data.categoryList
      }
    }
  },
  mounted () {
    this.getDatailInfo()
  }
}
</script>
<style lang="stylus" scoped>
	.content
		height: 20rem
</style>



