<template>
    <div class="icons">
         <swiper :options='swiperOption'>
            <swiper-slide v-for="(page, index) of pages" :key="index">
                <div class="icon" v-for="item of page" :key="item.id">
                     <div class="icon-img"><img class="icon-img-content" :src="item.imgUrl" />
                     </div>
                     <p class="icons-desc">{{item.desc}}</p>
                </div>
        </swiper-slide>
        </swiper>
        
    </div>
</template>
<script>
  export default{
    name:'HomeIcons',
    props: {
        list: Array
    },
    data () {
        return{
            swiperOption:{
                autoplay: false
            }
        }
    },
    computed: {
        pages () {
           const pages = []
           this.list.forEach((item, index) => {
            const page = Math.floor(index / 8)
            if (!pages[page]) {
                pages[page] = []
            }
            pages[page].push(item)
           })
           return pages
        }
    }
  }  
</script>
<style lang="stylus" scoped>
    .icons >>> .swiper-container
        height: 0
        padding-bottom: 50%
        // background blue
        .icon
            overflow: hidden
            float: left
            width: 25%
            padding-bottom: 25%
            // background: green
            position:relative
            .icon-img
                overflow: hidden
                position: absolute
                top: 0
                left: 0
                right: 0
                bottom: .44rem
                box-sizing: border-box
                padding: .1rem
                // background: yellow
                .icon-img-content
                    display: block
                    margin: 0 auto
                    height: 100%
            .icons-desc
                position: absolute
                left: 0
                right: 0
                bottom: 0
                height: .44rem
                line-height: .44rem
                text-align: center
                overflow: hidden
                color: $darkTextColor
                ellipsis()
</style>


