<template>
	<div>
		<div class="recommend-title">周末去哪儿</div>
		<ul>
			<li class="item border-bottom" v-for="item of list" :key="item.id" >
				<div class="item-img-warpper">
					<img class="item-img" :src="item.imgUrl">
				</div>
				<div class="item-info">
					<p class="item-title">{{item.title}}</p>
					<p class="item-desc">{{item.desc}}</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'HomeWeekend',
	props: {
		list: Array
	}
}
</script>
<style lang="stylus" scoped>
@import '~@/assets/styles/mixins.styl'
	.recommend-title
		background: #eee
		line-height: .9rem
		text-indent: .2rem
	.item-img-warpper
		overflow: hidden
		height: 0
		padding-bottom: 37.09%
		.item-img
			width: 100%
	.item-info
		padding: .1rem
		.item-title
			line-height: .54rem
			font-size: .32rem
			ellipsis()
		.item-desc
			line-height: .4rem
			font-size: .28rem
			color: #616161
			ellipsis()
	</style>


