import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Datail from '@/pages/datail/Datail'

Vue.use(Router)

export default new Router({
routes: [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/city',
    name: 'City',
    component: City
  },
  {
    path: '/datail/:id',
    name: 'Datail',
    component: Datail
  }],
   scrollBehavior (to, from, savedPosition) {
      return {x: 0, y: 0}
   }
})



