<template>
<div>
  <div class="item"
  v-for="(item,index) of list"
  >
    <div class="item-title">
      <div class="item-title-icon"></div>
      {{item.title}}
    </div>
    <div v-if="item.children" class="item-children">
     <datail-list :list="item.children"></datail-list>
    </div>
  </div>
</div>
</template>
<script>
  export default {
    name: 'DatailList',
    props: {
      list: Array
    }
  }
</script>
<style lang="stylus" scoped>
  .item-title
    line-height: .8rem
    font-size: .32rem
    padding: .2rem
    .item-title-icon
      position: relative
      left: .06rem
      top: .06rem
      display: inline-block
      width: .36rem
      height: .36rem
      background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
      margin-right: .1rem
      background-size: .4rem 3rem
  .item-children
    padding: 0 .1rem
</style>



