<template>
	<div class="list" ref="wrapper">
		<div>
		<div class="arer">
			<div class="title border-topbottom">当前城市</div>
			<div class="button-list">
				<div class="button-wrapper">
					<div class="button">{{this.$store.state.city}}</div>
				</div>
			</div>
		</div>
		<div class="arer">
			<div class="title border-topbottom">热门城市</div>
			<div class="button-list">
				<div
					class="button-wrapper"
					v-for="item of hot"
					:key="item.id"
					@click="handleClityClick(item.name)"
					>
					<div class="button">{{item.name}}</div>
				</div>
			</div>
		</div>
		<div class="arer" v-for='(item, key) of cities' :key='key' :ref='key'>
			<div class="title border-topbottom">{{key}}</div>
			<div class="item-list"
			v-for='innerItem of item'
			:key='innerItem.id'
			@click="handleClityClick(innerItem.name)"
			>
				<div class="item border-bottom">{{innerItem.name}}</div>
			</div>
		</div>
		</div>
		</div>
</template>

<script>
import Bscroll from 'better-scroll'
export default {
	name:'CityList',
	props:{
		hot: Array,
		cities: Object,
		letter: String
	},
	methods: {
		handleClityClick(city){
			this.$store.commit('changeCity',city)
			this.$router.push('./')
		}
	},
	mounted () {

		this.scroll = new Bscroll(this.$refs.wrapper)
	},
	watch: {
		letter () {
			if(this.letter){
				const element = this.$refs[this.letter][0]
				this.scroll.scrollToElement(element)
			}
		}
	}
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl'
	.border-topbottom
		&:before
			background: #ccc
		&:after
			background: #ccc
	.border-bottom
		&:before
			background: #ccc
	.list
		overflow: hidden
		position: absolute
		top: 1.58rem
		left: 0
		right: 0
		bottom: 0
	.title
		line-height: .54rem
		background: #eee
		padding-left: .2rem
		color: #666
		font-size: .26rem
	.button-list
		padding: .1rem .6rem .1rem .1rem
		overflow: hidden
		.button-wrapper
			float: left
			width: 32.33%
			.button
				margin: .1rem
				padding: .1rem 0
				text-align: center
				border: .02rem solid #ccc
				border-radius: .06rem
	.item-list
		.item
			line-height: .76rem
			padding-left: .2rem
</style>



