<template>
  <div class="wrapper">
  <swiper :options="swiperOption">
    <!-- slides -->
    <swiper-slide v-if="showSwiper"  v-for="item of list" :key="item.id">
        <img class="swiper-img" :src="item.imgUrl" alt=""></swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper>
  </div>
</template>

<script>
export default {
    name:'HomeSwiper',
    props:{
      list: Array
    },
    data () {
        return {
            swiperOption: {pagination: '.swiper-pagination', 
           autoplay: 2000}
            
        }

    },
    computed: {
      showSwiper () {
        return this.list.length
      }
    }
}
</script>

<style lang="stylus" scoped>
    .wrapper >>>.swiper-pagination-bullet-active
        background: #fff !important
        margin-bottom: .1rem
    .wrapper
        overflow: hidden
        width: 100%
        height: 0
        padding-bottom: 31.25%
        background: #eee
    .swiper-img
       width: 100%
</style>


